<template>
  <div class="works-management">
    <div class="page-header">
      <div class="header-content">
        <h2>作品管理</h2>
        <p>管理您的所有作品，包括摄影、绘画、数字艺术等</p>
      </div>
      <div class="header-actions">
        <t-button theme="primary" @click="goToCreate">
          <t-icon name="add" />
          添加作品
        </t-button>
      </div>
    </div>
    
    <!-- 筛选和搜索 -->
    <t-card class="filters-card">
      <div class="filters">
        <div class="filter-group">
          <t-input
            v-model="filters.search"
            placeholder="搜索作品..."
            prefix-icon="search"
            @input="handleSearch"
            clearable
          />
        </div>
        
        <div class="filter-group">
          <t-select
            v-model="filters.category"
            placeholder="选择分类"
            @change="handleFilter"
            clearable
          >
            <t-option value="" label="全部分类" />
            <t-option value="photography" label="摄影" />
            <t-option value="painting" label="绘画" />
            <t-option value="digital" label="数字艺术" />
            <t-option value="cityscape" label="城市印象" />
          </t-select>
        </div>
        
        <div class="filter-group">
          <t-select
            v-model="filters.published"
            placeholder="发布状态"
            @change="handleFilter"
            clearable
          >
            <t-option value="" label="全部状态" />
            <t-option :value="true" label="已发布" />
            <t-option :value="false" label="未发布" />
          </t-select>
        </div>
        
        <div class="filter-group">
          <t-button theme="default" variant="outline" @click="resetFilters">
            <t-icon name="refresh" />
            重置
          </t-button>
        </div>
      </div>
    </t-card>
    
    <!-- 批量操作 -->
    <div class="batch-actions" v-if="selectedWorks.length > 0">
      <div class="batch-info">
        已选择 {{ selectedWorks.length }} 个作品
      </div>
      <div class="batch-buttons">
        <t-button theme="success" @click="batchPublish(true)">
          批量发布
        </t-button>
        <t-button theme="warning" @click="batchPublish(false)">
          批量隐藏
        </t-button>
        <t-button theme="danger" @click="batchDelete">
          批量删除
        </t-button>
      </div>
    </div>
    
    <!-- 作品列表 -->
    <t-card class="works-table">
      <t-table
        :data="paginatedWorks.items"
        :columns="columns"
        :loading="isLoading"
        :selected-row-keys="selectedWorks"
        @select-change="handleSelectChange"
        row-key="id"
      >
        <!-- 缩略图 -->
        <template #thumbnail="{ row }">
          <img
            :src="row.thumbnail"
            :alt="row.title"
            class="thumbnail"
            @error="handleImageError"
          />
        </template>
        
        <!-- 标题 -->
        <template #title="{ row }">
          <div class="title-cell">
            <h4>{{ row.title }}</h4>
            <p>{{ row.description }}</p>
          </div>
        </template>
        
        <!-- 分类 -->
        <template #category="{ row }">
          <t-tag :theme="getCategoryTheme(row.category)">
            {{ getCategoryName(row.category) }}
          </t-tag>
        </template>
        
        <!-- 标签 -->
        <template #tags="{ row }">
          <div class="tags-cell">
            <t-tag
              v-for="tag in row.tags.slice(0, 3)"
              :key="tag"
              size="small"
              theme="default"
              variant="light"
            >
              {{ tag }}
            </t-tag>
            <span v-if="row.tags.length > 3" class="more-tags">
              +{{ row.tags.length - 3 }}
            </span>
          </div>
        </template>
        
        <!-- 状态 -->
        <template #status="{ row }">
          <t-tag :theme="row.isPublished ? 'success' : 'warning'">
            {{ row.isPublished ? '已发布' : '未发布' }}
          </t-tag>
        </template>
        
        <!-- 更新时间 -->
        <template #updatedAt="{ row }">
          {{ formatDate(row.updatedAt) }}
        </template>
        
        <!-- 操作 -->
        <template #action="{ row }">
          <div class="action-buttons">
            <t-button theme="default" variant="text" size="small" @click="previewWork(row)">
              <t-icon name="browse" />
            </t-button>
            <t-button theme="default" variant="text" size="small" @click="editWork(row)">
              <t-icon name="edit" />
            </t-button>
            <t-button theme="default" variant="text" size="small" @click="togglePublish(row)">
              <t-icon :name="row.isPublished ? 'eye-close' : 'eye'" />
            </t-button>
            <t-popconfirm
              content="确定要删除这个作品吗？"
              @confirm="deleteWork(row.id)"
            >
              <t-button theme="danger" variant="text" size="small">
                <t-icon name="delete" />
              </t-button>
            </t-popconfirm>
          </div>
        </template>
      </t-table>
      
      <!-- 分页 -->
      <div class="pagination">
        <t-pagination
          v-model="pagination.page"
          :total="paginatedWorks.total"
          :page-size="pagination.limit"
          @change="handlePageChange"
          show-sizer
          @page-size-change="handlePageSizeChange"
        />
      </div>
    </t-card>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { MessagePlugin, DialogPlugin } from 'tdesign-vue-next'
import { useWorksStore } from '@/stores/works'
import type { Work, WorkCategory } from '@/types/admin'

const router = useRouter()
const worksStore = useWorksStore()

const isLoading = ref(false)
const selectedWorks = ref<string[]>([])

// 筛选条件
const filters = reactive({
  search: '',
  category: '',
  published: '' as boolean | ''
})

// 分页配置
const pagination = reactive({
  page: 1,
  limit: 10
})

// 表格列配置
const columns = [
  {
    colKey: 'select-row',
    type: 'multiple-select',
    width: 50
  },
  {
    colKey: 'thumbnail',
    title: '缩略图',
    width: 100
  },
  {
    colKey: 'title',
    title: '作品信息',
    width: 300
  },
  {
    colKey: 'category',
    title: '分类',
    width: 120
  },
  {
    colKey: 'tags',
    title: '标签',
    width: 200
  },
  {
    colKey: 'status',
    title: '状态',
    width: 100
  },
  {
    colKey: 'updatedAt',
    title: '更新时间',
    width: 150
  },
  {
    colKey: 'action',
    title: '操作',
    width: 200
  }
]

// 分页作品数据
const paginatedWorks = computed(() => {
  return worksStore.getPaginatedWorks({
    page: pagination.page,
    limit: pagination.limit,
    search: filters.search || undefined,
    category: filters.category || undefined,
    published: filters.published !== '' ? filters.published : undefined
  })
})

// 获取分类名称
const getCategoryName = (category: WorkCategory) => {
  const names: Record<WorkCategory, string> = {
    photography: '摄影',
    painting: '绘画',
    digital: '数字艺术',
    cityscape: '城市印象'
  }
  return names[category] || category
}

// 获取分类主题
const getCategoryTheme = (category: WorkCategory) => {
  const themes: Record<WorkCategory, string> = {
    photography: 'primary',
    painting: 'success',
    digital: 'warning',
    cityscape: 'danger'
  }
  return themes[category] || 'default'
}

// 格式化日期
const formatDate = (dateString: string) => {
  const date = new Date(dateString)
  return date.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit'
  })
}

// 跳转到创建页面
const goToCreate = () => {
  router.push('/admin/works/create')
}

// 处理搜索
const handleSearch = () => {
  pagination.page = 1
}

// 处理筛选
const handleFilter = () => {
  pagination.page = 1
}

// 重置筛选
const resetFilters = () => {
  filters.search = ''
  filters.category = ''
  filters.published = ''
  pagination.page = 1
}

// 处理分页变化
const handlePageChange = (page: number) => {
  pagination.page = page
}

// 处理分页大小变化
const handlePageSizeChange = (size: number) => {
  pagination.limit = size
  pagination.page = 1
}

// 处理选择变化
const handleSelectChange = (selectedRowKeys: string[]) => {
  selectedWorks.value = selectedRowKeys
}

// 预览作品
const previewWork = (work: Work) => {
  window.open(`/portfolio/${work.id}`, '_blank')
}

// 编辑作品
const editWork = (work: Work) => {
  router.push(`/admin/works/${work.id}/edit`)
}

// 切换发布状态
const togglePublish = (work: Work) => {
  worksStore.updateWork(work.id, { isPublished: !work.isPublished })
  MessagePlugin.success(`作品已${work.isPublished ? '隐藏' : '发布'}`)
}

// 删除作品
const deleteWork = (id: string) => {
  if (worksStore.deleteWork(id)) {
    MessagePlugin.success('作品已删除')
  } else {
    MessagePlugin.error('删除失败')
  }
}

// 批量发布/隐藏
const batchPublish = (published: boolean) => {
  worksStore.updatePublishStatus(selectedWorks.value, published)
  selectedWorks.value = []
  MessagePlugin.success(`已${published ? '发布' : '隐藏'}选中的作品`)
}

// 批量删除
const batchDelete = () => {
  const dialog = DialogPlugin({
    header: '批量删除确认',
    body: `确定要删除选中的 ${selectedWorks.value.length} 个作品吗？此操作不可恢复。`,
    confirmBtn: '确定删除',
    cancelBtn: '取消',
    onConfirm: () => {
      selectedWorks.value.forEach(id => {
        worksStore.deleteWork(id)
      })
      selectedWorks.value = []
      MessagePlugin.success('批量删除成功')
      dialog.destroy()
    }
  })
}

// 处理图片错误
const handleImageError = (event: Event) => {
  const img = event.target as HTMLImageElement
  img.src = '/src/assets/images/avatar/placeholder.jpg'
}

// 初始化
onMounted(() => {
  worksStore.initWorks()
})
</script>

<style lang="scss" scoped>
@import "@/styles/variables.scss";

.works-management {
  max-width: 1200px;
  margin: 0 auto;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: $spacing-xl;
  
  .header-content {
    h2 {
      margin: 0 0 $spacing-xs 0;
      color: $text-primary;
      font-size: 1.8rem;
      font-weight: 600;
    }
    
    p {
      margin: 0;
      color: $text-secondary;
    }
  }
}

.filters-card {
  margin-bottom: $spacing-lg;
  
  .filters {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr auto;
    gap: $spacing-md;
    
    @media (max-width: $breakpoint-lg) {
      grid-template-columns: 1fr;
    }
    
    .filter-group {
      display: flex;
      align-items: center;
    }
  }
}

.batch-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-md $spacing-lg;
  background: mix($primary-color, $background-color, 5%);
  border-radius: 8px;
  margin-bottom: $spacing-lg;
  
  .batch-info {
    color: $primary-color;
    font-weight: 500;
  }
  
  .batch-buttons {
    display: flex;
    gap: $spacing-sm;
  }
}

.works-table {
  .thumbnail {
    width: 60px;
    height: 60px;
    border-radius: 8px;
    object-fit: cover;
  }
  
  .title-cell {
    h4 {
      margin: 0 0 $spacing-xs 0;
      color: $text-primary;
      font-size: 0.95rem;
      font-weight: 500;
      line-height: 1.3;
    }
    
    p {
      margin: 0;
      color: $text-secondary;
      font-size: 0.8rem;
      line-height: 1.3;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  
  .tags-cell {
    display: flex;
    gap: $spacing-xs;
    align-items: center;
    
    .more-tags {
      font-size: 0.8rem;
      color: $text-tertiary;
    }
  }
  
  .action-buttons {
    display: flex;
    gap: $spacing-xs;
  }
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: $spacing-lg;
}

@media (max-width: $breakpoint-md) {
  .page-header {
    flex-direction: column;
    gap: $spacing-md;
  }
  
  .batch-actions {
    flex-direction: column;
    gap: $spacing-md;
    text-align: center;
  }
}
</style>